/** Media Queries **/
.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #84d9bc;
}

.section-pd {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .section-pd {
    padding: 8rem 0;
  }
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

a {
  text-decoration: none;
}
a:hover {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 3rem;
  display: inline-block;
  margin: 5rem 0 3rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.6rem;
  }
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.container {
  width: 92%;
  max-width: 130rem;
  margin: 0 auto;
}

/* HEADER SECTION */
.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../images/header.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  height: 100vh;
}
.header__flex {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__img {
  max-width: 20rem;
  padding: 5rem 0;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__img {
    margin: 0;
  }
}
.header__content {
  padding-bottom: 13rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__content {
    align-items: flex-start;
    padding-bottom: 13rem;
  }
}
.header__heading {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 4.5rem;
    text-align: left;
    font-size: 5rem;
    max-width: 55%;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    font-size: 6.5rem;
    width: 50%;
  }
}
.header__button {
  margin-top: 1rem;
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  -webkit-transition: background-color 0.2s ease-in-out;
  -moz-transition: background-color 0.2s ease-in-out;
  -ms-transition: background-color 0.2s ease-in-out;
  -o-transition: background-color 0.2s ease-in-out;
  border: none;
  font-size: 2.5rem;
}
.header__button:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
    display: inline-block;
  }
}

@media (min-width: 768px) {
  .about__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.about__heading {
  color: #313c4e;
}
.about__heading::after {
  content: "";
  display: block;
  height: 1rem;
  margin-top: -1.4rem;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
}

.benefits {
  background-image: linear-gradient(#84d9bc 0%, #84d9bc 100%), url("");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
@media (min-width: 768px) {
  .benefits__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.benefits__content {
  grid-column: 1/4;
}
.benefits__heading {
  margin-top: 0;
  color: #313c4e;
}
@media (min-width: 768px) {
  .benefits__heading {
    margin-top: 5rem;
  }
}
.benefits__heading::after {
  content: "";
  display: block;
  height: 1rem;
  margin-top: -1.4rem;
  width: calc(100% + 1rem);
  background-color: #FFFFFF;
}
.benefits__illustration {
  width: 90%;
  margin: 4rem auto 0;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .benefits__illustration {
    grid-column: 4/6;
    width: auto;
    margin: 0;
  }
}
.benefits__illustration:hover {
  transform: scale(1.1) rotate(5deg);
  -webkit-transform: scale(1.1) rotate(5deg);
  -moz-transform: scale(1.1) rotate(5deg);
  -ms-transform: scale(1.1) rotate(5deg);
  -o-transform: scale(1.1) rotate(5deg);
}

.statistics__heading {
  margin-top: 0;
  color: #313c4e;
}
@media (min-width: 768px) {
  .statistics__heading {
    margin-top: 5rem;
  }
}
.statistics__heading::after {
  content: "";
  display: block;
  height: 1rem;
  margin-top: -1.4rem;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
}
.statistics__img {
  margin-top: 3rem;
  box-shadow: 0px 8px 15px rgba(57, 70, 89, 0.5);
}

.plans {
  background-image: linear-gradient(#313c4e 0%, #313c4e 100%), url("");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
.plans__heading {
  margin-top: 0;
  color: #FFFFFF;
}
.plans__heading::after {
  content: "";
  display: block;
  height: 1rem;
  margin-top: -1.4rem;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
}
.plans__text {
  color: #FFFFFF;
}

.our-plans {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .our-plans {
    max-width: 80rem;
    flex-direction: row;
    margin: 10rem auto 5rem;
  }
}
@media (min-width: 1024px) {
  .our-plans {
    margin-top: 15rem;
  }
}

.plan {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 15rem 22% 8rem;
  border-bottom: 1px solid #dddddd;
}
@media (min-width: 768px) {
  .plan {
    flex-grow: 1;
    padding: 20rem 3rem 5rem;
  }
}
@media (min-width: 1024px) {
  .plan {
    padding-inline: 6rem;
  }
}
@media (min-width: 768px) {
  .plan {
    border: none;
    transition: transform 0.3s ease-in-out;
    -webkit-transition: transform 0.3s ease-in-out;
    -moz-transition: transform 0.3s ease-in-out;
    -ms-transition: transform 0.3s ease-in-out;
    -o-transition: transform 0.3s ease-in-out;
  }
  .plan:hover {
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
  }
}
.plan__heading, .plan__price, .plan__item {
  color: #313c4e;
}
.plan__heading {
  margin: 0;
}
.plan__price {
  font-weight: 700;
  text-transform: uppercase;
}
.plan__item {
  text-align: center;
  margin-bottom: 1.5rem;
}
.plan__item:last-of-type {
  margin-bottom: 0;
}
.plan__button {
  margin-top: 2rem;
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  -webkit-transition: background-color 0.2s ease-in-out;
  -moz-transition: background-color 0.2s ease-in-out;
  -ms-transition: background-color 0.2s ease-in-out;
  -o-transition: background-color 0.2s ease-in-out;
  border: none;
}
.plan__button:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .plan__button {
    width: auto;
    display: inline-block;
  }
}
.plan__button--premium {
  border: 1px solid #FFFFFF;
}
.plan__button--premium:hover {
  color: #313c4e;
  background-color: #FFFFFF;
}
.plan--start {
  background-image: url("../images/icono_inicio.svg");
  background-size: 10rem;
  background-repeat: no-repeat;
  background-position: 50% 4rem;
}
.plan--premium {
  order: -1;
  background-color: #84d9bc;
  background-image: url("../images/icono_negocio.svg");
  background-size: 10rem;
  background-repeat: no-repeat;
  background-position: 50% 4rem;
}
@media (min-width: 768px) {
  .plan--premium {
    order: 0;
    z-index: 100;
    margin: -6rem 0;
    border-radius: 2rem;
    -webkit-border-radius: 2rem;
    -moz-border-radius: 2rem;
    -ms-border-radius: 2rem;
    -o-border-radius: 2rem;
    background-position: 50% 7rem;
    background-size: 14rem;
    padding-top: 25rem;
  }
}
.plan--pro {
  background-image: url("../images/icono_pro.svg");
  background-size: 10rem;
  background-repeat: no-repeat;
  background-position: 50% 4rem;
}

@media (min-width: 1024px) {
  .testimonials__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.testimonials__heading {
  margin-top: 0;
}
.testimonials__heading::after {
  content: "";
  display: block;
  height: 1rem;
  margin-top: -1.4rem;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
}
.testimonials__illustration {
  max-width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .testimonials__illustration {
    max-width: 60%;
  }
}
@media (min-width: 1024px) {
  .testimonials__illustration {
    max-width: 90%;
    grid-column: 1/3;
    transition: transform 0.3s ease-in-out;
    -webkit-transition: transform 0.3s ease-in-out;
    -moz-transition: transform 0.3s ease-in-out;
    -ms-transition: transform 0.3s ease-in-out;
    -o-transition: transform 0.3s ease-in-out;
  }
  .testimonials__illustration:hover {
    transform: scale(1.05) rotate(-4deg);
    -webkit-transform: scale(1.05) rotate(-4deg);
    -moz-transform: scale(1.05) rotate(-4deg);
    -ms-transform: scale(1.05) rotate(-4deg);
    -o-transform: scale(1.05) rotate(-4deg);
  }
}
.testimonials__img {
  margin: 0 auto;
}
.testimonials__content {
  margin-top: 7rem;
}
@media (min-width: 1024px) {
  .testimonials__content {
    margin-top: 0;
    grid-column: 3/6;
  }
}
.testimonials__cards {
  margin-top: 2rem;
}

.testimonial {
  border: 5px solid #313c4e;
  background-color: #71d3b1;
  padding: 2rem;
  margin-bottom: 4rem;
  position: relative;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 6rem;
    max-width: 70%;
  }
  .testimonial:nth-child(1) {
    margin-left: 5%;
  }
  .testimonial:nth-child(2) {
    margin-left: 15%;
  }
  .testimonial:nth-child(3) {
    margin-left: 25%;
  }
}
.testimonial__text {
  margin: 0;
  padding-left: 6rem;
}
.testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  position: absolute;
  background-image: url("../images/comilla.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.footer {
  background-image: linear-gradient(#313c4e 0%, #313c4e 100%), url("");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__img {
  max-width: 70%;
  margin: 0 auto 7rem;
}
@media (min-width: 768px) {
  .footer__img {
    margin: 0 auto;
  }
}
.footer__copyright {
  margin-top: 5rem;
  border-top: 2px solid #84d9bc;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 1.6rem;
  padding: 2rem 0 0;
}

.widget {
  width: 70%;
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .widget {
    width: auto;
    margin: unset;
  }
}
.widget__heading {
  text-align: left;
  color: #84d9bc;
}
.widget__link {
  display: block;
  color: #FFFFFF;
  margin-bottom: 1.5rem;
  font-size: 1.6rem;
}

.scroll-down-buttons {
  position: fixed;
  bottom: 30px;
  right: 45%;
  z-index: 999;
}
@media (min-width: 768px) {
  .scroll-down-buttons {
    right: 50%;
    left: 50%;
  }
}

#scroll-down-button {
  background-color: #FFFFFF;
  border: none;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  transition: background-color 0.3s ease-in-out;
  -webkit-transition: background-color 0.3s ease-in-out;
  -moz-transition: background-color 0.3s ease-in-out;
  -ms-transition: background-color 0.3s ease-in-out;
  -o-transition: background-color 0.3s ease-in-out;
  animation: bounce 1s ease-in-out infinite;
  -webkit-animation: bounce 1s ease-in-out infinite;
}
#scroll-down-button img {
  width: 30px;
  height: 30px;
  margin: 0.2rem auto 0;
}

#scroll-down-button:hover {
  background-color: #313c4e;
}

/* Bounce animation */
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}